import React from 'react'
import {
  CContainer,
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CCollapse,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
  CFade,
  CForm,
  CFormGroup,
  CFormText,
  CValidFeedback,
  CInvalidFeedback,
  CTextarea,
  CInput,
  CInputFile,
  CInputCheckbox,
  CInputRadio,
  CInputGroup,
  CInputGroupAppend,
  CInputGroupPrepend,
  CDropdown,
  CInputGroupText,
  CLabel,
  CSelect,
  CRow,
  CSwitch,
  CBadge,
  CDataTable,
  hover
} from '@coreui/react'

import usersData from '../../users/UsersData'
import { DocsLink } from 'src/reusable'
const fields = ['NoUrutAlat','JenisAset', 'Jumlah', 'Aksi']



const currDate = new Date().toLocaleDateString();
  

const PeminjamanBaru = () => {
  const [collapsed, setCollapsed] = React.useState(true)
  const [showElements, setShowElements] = React.useState(true)

  return (
    <>
      <CRow>
        <CCol xs="12">
          <CFade timeout={300} in={showElements} unmountOnExit={true}>
            <CCard>
              <CCardHeader>
                <h4> Form Peminjam   </h4>
                {/* <div className="card-header-actions">
                  <CButton color="link" className="card-header-action btn-setting">
                    <CIcon name="cil-settings" />
                  </CButton>
                  <CButton 
                    color="link" 
                    className="card-header-action btn-minimize" 
                    onClick={() => setCollapsed(!collapsed)}
                  >
                    <CIcon name={ collapsed ? "cil-arrow-top" : "cil-arrow-bottom"} />
                  </CButton>
                  <CButton 
                    color="link" 
                    className="card-header-action btn-close" 
                    onClick={() => setShowElements(false)}
                  >
                    <CIcon name="cil-x" />
                  </CButton>
                </div> */}
              </CCardHeader>
              <CCollapse show={collapsed} timeout={1000}>
                <CCardBody>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="disabled-input">Admin Peminjam</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CInput id="disabled-input" name="disabled-input" placeholder="Muhammad Emirzaki" disabled />
                  </CCol>
                </CFormGroup>
           
                <CForm action="" method="post" className="form-horizontal">
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="hf-email">Nama Peminjam</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CInput type="code" id="hf-code" name="hf-code" placeholder="Masukkan Nama Peminjam..." autoComplete="Kode Pengadaan" />
                    {/* <CFormText className="help-block">Please enter your email</CFormText> */}
                  </CCol>
                </CFormGroup>
                </CForm>
                <CForm action="" method="post" className="form-horizontal">
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="hf-email">Unit/Vendor</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CInput type="code" id="hf-code" name="hf-code" placeholder="Masukkan Unit/Vendor..." autoComplete="Kode Pengadaan" />
                    {/* <CFormText className="help-block">Please enter your email</CFormText> */}
                  </CCol>
                </CFormGroup>
                </CForm>
                  <CForm className="form-horizontal">
                  <CRow>
                <CCol xs="12">
                <CFormGroup row>
                  <CCol lg="3">
                    <CLabel htmlFor="select">Nomor Urut Aset</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CSelect custom name="select" id="select">
                      <option value="0">Please select</option>
                      <option value="1">Option #1</option>
                      <option value="2">Option #2</option>
                      <option value="3">Option #3</option>
                    </CSelect>
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="select">Jenis Aset</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CSelect custom name="select" id="select">
                      <option value="0">Please select</option>
                      <option value="1">Option #1</option>
                      <option value="2">Option #2</option>
                      <option value="3">Option #3</option>
                    </CSelect>
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="select">Tipe Aset</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CSelect custom name="select" id="select">
                      <option value="0">Please select</option>
                      <option value="1">Option #1</option>
                      <option value="2">Option #2</option>
                      <option value="3">Option #3</option>
                    </CSelect>
                  </CCol>
                </CFormGroup>
               
               
               
                  
                </CCol>
              </CRow> 
              <CFormGroup row>
               
               <CCol md="3">
                 <CLabel htmlFor="select">Kondisi Aset</CLabel>
               </CCol>
               <CCol xs="5" >
                 <CSelect custom name="select" id="select">
                   <option value="0">Please select</option>
                   <option value="1">Option #1</option>
                   <option value="2">Option #2</option>
                   <option value="3">Option #3</option>
                   
                 </CSelect>
                 {/* <div className="form-actions" style={{padding: '5px 5px'}}>
                   <CButton type="submit" href="/login" color="primary">Simpan</CButton>
                 </div> */}
               </CCol>
             </CFormGroup>                     
          
            <hr></hr>
              
                                  
              <CRow>
                <CCol >
                <CFormGroup row>
                  <CCol xs="12">
                    <CLabel htmlFor="date-input">Tanggal Peminjam</CLabel>
                  </CCol>
                  <CCol xs="" >
                    <CInput type="date" id="date-input" name="date-input" placeholder="date" />
                  </CCol>
                </CFormGroup>
                </CCol>
                
              
                <CCol xs="">
                <CFormGroup row>
                  <CCol xs="12">
                    <CLabel htmlFor="date-input">Tanggal Rencana Kembali </CLabel>
                  </CCol>
                  <CCol xs="" >
                    <CInput type="date" id="date-input" name="date-input" placeholder="date" />
                  </CCol>
                </CFormGroup>
                  
                </CCol>
           
              </CRow>
              
            <hr></hr>
            
              
                  <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="textarea-input">Deskripsi</CLabel>
                  </CCol>
                  <CCol xs="10" md="9">
                    <CTextarea 
                      name="textarea-input" 
                      id="textarea-input" 
                      rows="5"
                      placeholder="Deskripsikan Keperluan Peminjaman..." 
                    />
                  </CCol>
                </CFormGroup>
                <CButton type="submit" color="primary">Input Transaksi</CButton>
                <hr></hr>
                
               
                <CRow>
        <CCol xs="12" md="0">
          <CCard>
            <CCardHeader>
          <center> <h5>Daftar Barang Peminjaman</h5></center> 
             
            </CCardHeader>
            <CCardBody>
              
            <CDataTable
              items={usersData}
              fields={fields}
              itemsPerPage={5}
              hover
      
              scopedSlots = {{
                'Aksi':
                  (item)=>(
                    <td>
                      
          
                     
                     <div className="form-actions">
                       <CButton color="danger">Hapus</CButton>
                     
                    </div>
                    </td>
                    
                  )

              }}
            />
            </CCardBody>
          </CCard>
        </CCol>
</CRow>

                    <div className="form-actions">
                      <CButton type="submit" color="primary">Tambah Peminjaman</CButton>
                      <CButton color="secondary">Cetak</CButton>
                    </div>
                  </CForm>
                </CCardBody>
              </CCollapse>
            </CCard>
          </CFade>
        </CCol>
      </CRow>
    </>
  )
}

export default PeminjamanBaru
